:root {
	--brand-grey-dark: #333;
	--brand-grey: #bbb;
	--brand-grey-light: #ddd;
	--brand-grey-dimmed: #fafafa;
	--brand-white: #fff;
	--brand-blue: #8b98a9;
	--brand-orange: #ffc107;
	--brand-red: #ca2935;

	--green: #5fb359;
	--red: #d80606;
	--orange: #b35f36;
	--yellow: #eac146;

	--font-family-base: 'Fira Code', 'Fira Mono', Menlo, Monaco, Consolas,
		'Courier New', monospace;

	--font-family-monospace: 'Fira Code', 'Fira Mono', Menlo, Monaco, Consolas,
		'Courier New', monospace;

	--font-size-root: 16px;
	--font-size-base: 1rem;
	--font-size-large: 1.2rem;
	--font-size-small: 0.85rem;
	--font-size-mini: 0.65rem;

	--font-size-text: 1rem;
	--font-size-label: 1rem;
	--font-size-title: 1.4rem;

	--font-size-h1: 3rem;
	--font-size-h2: 1.7rem;
	--font-size-h3: 1.4rem;
	--font-size-h4: 1.2rem;
	--font-size-h5: 1.1rem;

	--font-weight-base: 400;
	--font-weight-bold: 600;
	--line-height: 1.6;

	--break-point--small: 640px;
	--break-point--medium: 860px;
	--break-point--large: 1140px;
	--break-point--huge: 1400px;

	--spacer: 2rem;
	--border-radius: 0.2rem;

	--layout-max-width: 85rem;

	--checkbox-size: 1.6em;
}
